import { Card, CardContent, CardMedia, makeStyles } from '@material-ui/core';
import GatsbyImage, { FluidObject } from 'gatsby-image';
import React, { ReactNode } from 'react';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    height: '200px',
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'column',
      height: 'auto',
    },
  },
  media: {
    width: '300px',
    [theme.breakpoints.down('sm')]: {
      width: '100%',
      height: '250px',
    },
  },
  image: {
    height: '100%',
  },
}));

export interface CardWithImageProps {
  children: ReactNode;
  imgObjectPosition?: string;
  fluidImageObject: FluidObject | FluidObject[];
}

export const CardWithImage = (props: CardWithImageProps) => {
  const { children, imgObjectPosition, fluidImageObject } = props;
  const styles = useStyles();
  return (
    <Card className={styles.root}>
      <CardMedia className={styles.media}>
        <GatsbyImage
          className={styles.image}
          alt="image"
          fluid={fluidImageObject}
          loading="eager"
          imgStyle={{ objectPosition: imgObjectPosition }}
        />
      </CardMedia>
      <CardContent>
        {children}
      </CardContent>
    </Card>
  );
};
